<template>
  <div class="demo-container">
    <van-nav-bar title="加密CID" right-text="去解密" left-arrow @click-left="onClickLeft" @click-right="onClickRight"
    />
    <van-cell-group>
      <van-field v-model="cid" required clearable label="Cid" icon="question" placeholder="请输入你的CID" @click-icon="()=>{this.popText='例如：盛景Cid为21297'
        this.show=true}" />
      <van-field v-model="key" type="password" label="Key" placeholder="请输入你的key" required />
    </van-cell-group>

    <div class="btn-container">
      <van-button class="button" size="large" @click="getEncodedCid(cid,key)">转换</van-button>
    </div>
    <!-- <button @click="getEncodedCid(cid,key)">转换</button> -->
    <van-popup class="pop-window" v-model="show">{{popText}}</van-popup>
    <div class="encode-value">
      <label>加密后值为：</label><span id="success_form_input">{{encodedCid}}</span>
      <button :class="{'none':encodedCid===''}" ref="copy" data-clipboard-action="copy" data-clipboard-target="#success_form_input"
        @click="copyLink">复制</button>
    </div>
  </div>
</template>

<script>
import Utils from '@/js/utils'
import { Field, CellGroup, Button, popup, NavBar, Toast } from 'vant'
export default {
  data() {
    return {
      cid: null,
      encodedCid: '',
      key: '',
      show: false,
      popText: ''
    }
  },
  mounted() {
    /*这里使用了刚开始注册到vue原型上的clipboard(使用注册到vue原型的插件的方法就是：this + 注册时命名名字)
*这里需要用到刚才给button命名的ref的名字，因为这是vue提供的获取DOM的方法，虽然可以通过引入jQuery来获取，但是不推荐这样做，具体原因可以看官方文档和网上论坛搜索
*这里是在挂载到DOM节点时将button按钮传入clipboard并存储起来，具体原因下面会有详述
*/
    this.copyBtn = new this.Clipboard(this.$refs.copy)
  },
  methods: {
    getEncodedCid(msg, key) {
      if (msg === '' || key === '') {
        Toast('请输入以上两值')
      } else {
        this.encodedCid = Utils.encryptByDESModeCBC(msg, key)
      }
    },
    onClickLeft() {
      window.history.go(-1)
    },
    onClickRight() {
      this.$router.push('/getCid')
    },
    copyLink() {
      /*这是点击按钮触发的点击事件，关于clipboard的使用就不再赘述了，上面介绍时已经讲述过，并且使用方法在官方文档上有*/
      const _this = this
      const clipboard = _this.copyBtn
      clipboard.on('success', function() {
        Toast(
          /*这是使用了element-UI的信息弹框*/
          '复制成功！'
        )
      })
      clipboard.on('error', function() {
        Toast('复制失败，请手动选择复制！')
      })
    }
  },
  components: {
    [Field.name]: Field,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [popup.name]: popup,
    [Toast.name]: Toast,
    [NavBar.name]: NavBar
  }
}
</script>

<style scoped lang="scss">
.demo-container {
  text-align: center;
  .none {
    display: none;
  }
  .pop-window {
    padding: 15px 10px;
    border-radius: 10px;
  }
  .btn-container {
    padding-top: 20px;
    width: 30%;
    margin: 0 auto;
  }
  .encode-value {
    color: #666;
    font-size: 14px;
    padding: 20px 15px;
    text-align: left;
    display: flex;
    align-items: center;
    height: 30px;
    line-height: 30px;
    span {
      color: #333;
      font-weight: bolder;
      flex: 1;
    }
    button {
      border: 1px solid #e5e5e5;
      border-radius: 3px;
      background-color: #fff;
      padding: 2px 8px;
    }
  }
}
</style>


