import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import getEncodeCid from '@/pages/getEncodeCid'
import getCid from '@/pages/getCid'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/getEncodeCid',
      component: getEncodeCid
    }, {
      path: '/getCid',
      component: getCid
    }
  ]
})



