<template>
  <section>
    <div class="header-container">
      <van-icon name="contact" color="#666"></van-icon>
      <van-search class="search-tab" placeholder="搜索音乐、视频、歌词、电台" :show-action="showAction" @search="onSearch"
        @cancel="onCancel" background="rgba(255,255,255,0)" v-model="musicName" @focus="onFocus" @blur="onBlur"
      />
    </div>
  </section>
</template>

<script>
import { Search, Icon } from 'vant'
export default {
  data() {
    return {
      musicName: '',
      showAction: false
    }
  },
  methods: {
    onCancel() {
      console.log(this.musicName)
    },
    onSearch() {
      alert(this.musicName)
    },
    onFocus() {
      this.showAction = true
    },
    onBlur() {
      this.showAction = false
    }
  },
  components: {
    [Search.name]: Search,
    [Icon.name]: Icon
  }
}
</script>

<style lang="scss">
.header-container {
  display: flex;
  align-items: center;
  padding-left: 20px;
  background-color: #fff;
}
.search-tab {
  font-size: 12px;
  width: 90%;
  display: inline-flex;
  .van-cell__title .van-icon {
    font-size: 18px;
  }
  .van-search__cancel {
    color: #bbb;
  }
  .van-cell {
    border-radius: 20px;
    background-color: #e5e5e5;
    opacity: 0.7;
  }
  .van-search__action {
    line-height: normal;
  }
}
</style>



