import crypto from 'crypto-js'

export default {
  encryptByDESModeCBC(msg, key) {
    try {
      const keyHex = crypto.enc.Utf8.parse(key)
      const ivHex = crypto.enc.Utf8.parse(key)
      const encrypted = crypto.DES.encrypt(msg, keyHex, {
        iv: ivHex,
        mode: crypto.mode.CBC,
        padding: crypto.pad.Pkcs7
      }
      )
      return encrypted.ciphertext.toString()
    } catch (e) {
      console.log(e)
      return ''
    }
  },
  decryptByDESModeCBC(ciphertext, key) {
    try {
      const keyHex = crypto.enc.Utf8.parse(key)
      const ivHex = crypto.enc.Utf8.parse(key)
      const decrypted = crypto.DES.decrypt({
        ciphertext: crypto.enc.Hex.parse(ciphertext)
      }, keyHex, {
          iv: ivHex,
          mode: crypto.mode.CBC,
          padding: crypto.pad.Pkcs7
        })
      return decrypted.toString(crypto.enc.Utf8)
    } catch (e) {
      console.log(e)
      return ''
    }
  }
}



