<template>
  <section>
    <p class="list-title">
      <span>{{listTile}}</span>
      <van-icon name="arrow"></van-icon>
    </p>
    <van-row>
      <van-col v-for="(item,index) in lists" :key="index" span="8">
        <div @click="listTabClick(index)">
          <div class="img-container">
            <img :src="item.imgUrl">
          </div>
          <div class="text-container">{{item.text}}</div>
        </div>
      </van-col>
    </van-row>
  </section>
</template>

<script>
import { Icon, Row, Col } from 'vant'
export default {
  props: {
    listTile: {
      type: String,
      default: ''
    },
    lists: {
      type: Array
    }
  },
  methods: {
    listTabClick(index) {
      console.log(index)
    }
  },
  components: {
    [Icon.name]: Icon,
    [Row.name]: Row,
    [Col.name]: Col
  }
}
</script>

<style lang="scss">
.list-title {
  display: flex;
  align-items: center;
  margin: 20px 0 8px;
  .van-icon {
    font-size: 12px;
  }
}
.img-container {
  margin: 0 auto;
  width: 95%;
  height: 95px;
  border-radius: 8px;
  overflow: hidden;
  img {
    width: 100%;
  }
}
.text-container {
  width: 95%;
  margin: 0 auto;
  font-size: 12px;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>





