<template>
  <div class="index-container">
    <home-header></home-header>
    <van-tabs v-model="active" swipeable :line-width=20 sticky :duration=0.4>
      <van-tab v-for="(item,index) in tabs" :title="item" :key="index">
        <template v-if="index===0">
          <van-list v-model="loading" :finished="finished" @load="onLoad">
            <div class="swipe-container">
              <van-swipe :autoplay="3000">
                <van-swipe-item v-for="(image, index) in bannerList" :key="index">
                  <img class="swipe-image" :src="image" />
                </van-swipe-item>
              </van-swipe>
            </div>
            <list :listTile="'推荐歌单'" :lists="recommendMusicList"></list>
          </van-list>
        </template>
        <template v-if="index===1">
          {{index}}
        </template>
      </van-tab>
    </van-tabs>
    <home-footer></home-footer>
  </div>
</template>

<script>
import homeHeader from './header'
import homeFooter from '@/components/bottom'
import list from './list'
import { Tab, Tabs, Swipe, SwipeItem, Lazyload, List } from 'vant'
export default {
  data() {
    return {
      tabs: ['个性推荐', '主播电台'],
      active: '',
      bannerList: [
        require('@/assets/banner1.jpg'),
        require('@/assets/banner2.png'),
        require('@/assets/banner3.png')
      ],
      loading: false,
      finished: true,
      recommendMusicList: [
        { imgUrl: require('@/assets/album1.png'), text: '我是一段测试文字' },
        { imgUrl: require('@/assets/album2.jpeg'), text: '我是一段测试文字' },
        { imgUrl: require('@/assets/album3.jpeg'), text: '我是一段测试文字' },
        { imgUrl: require('@/assets/album4.jpeg'), text: '我是一段测试文字' },
        { imgUrl: require('@/assets/album5.jpeg'), text: '我是一段测试文字' },
        {
          imgUrl: require('@/assets/album6.jpeg'),
          text: '我是一段测试文字，我是一段测试文字我是一段测试文字'
        }
      ]
    }
  },
  methods: {
    onLoad() {}
  },
  components: {
    homeHeader,
    homeFooter,
    list,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Lazyload.name]: Lazyload,
    [List.name]: List
  }
}
</script>

<style lang="scss">
.index-container {
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  [class*='van-hairline']::after {
    border: 0;
  }
  .van-hairline--top-bottom::after {
    border-width: 0;
  }
  .van-tabs {
    // min-height: 100%;
    // box-sizing: border-box;
    flex: 1;
    overflow: auto;
  }
  .van-list {
    padding: 5px 20px 0;
  }
  .swipe-container {
    height: 110px;
    width: 335px;
    border-radius: 8px;
    box-sizing: border-box;
    visibility: visible;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
    .van-swipe__track,
    .van-swipe-item {
      border-radius: 8px;
    }
  }
  .swipe-image {
    width: 100%;
  }
}
</style>




